import { Component, ViewChild } from '@angular/core';
import {ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-asignacion-pap-create',
  templateUrl: 'asignacion-pap-create.html',
})
export class AsignacionPapCreatePage {

  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  items: any=[];
  tiposus: any=[];
  public nombre		     : any;
  public cuitt		     : any;

  public nota		     : any;
  public evento		     : any;
  username: string = "";
  public cui		     : any;
  public idsus2		     : any;
  public idnombre2		     : any;
  public estado		     : any;
  private _ID                  : String;
 // private _IDSUSCRIPTOR                  : String;
  public image              	: any;
  public pageTitle           	: string;
  public idsuscriptor                : any;
  public idnombre                : any;
  public idcomprasaldo                : any;
  idempresa:any;

  isDisablednit :boolean=false;
  constructor(public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({
        cui        	    : ['', Validators.required],
        nota     : ['', Validators.required],
     });

     
     

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  
  this.idsuscriptor=this.navParams.data.record2.idsuscriptor;
  this.idnombre=this.navParams.data.record2.idnombre;
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
   // this.idsuscriptor=this.tiposus[3]
  this.cuitt=this.tiposus[2]
  this.cui=this.tiposus[2]
  this.idsus2=this.tiposus[3]
  this.idnombre2=this.tiposus[4]
    
          this.focused('a')

});

  }
  ionViewDidLoad() : void
  { 
    this.pageTitle="Solicitud de carné";
    if(this.navParams.get("record")==null)
    {
       
     
      
      this.isDisablednit =true
    }
    else {
      
       this._ID 				=	this.navParams.data.record._id;
    //   this.idsuscriptor 				=	this.navParams.data.record.idsuscriptor._id;
      // this._IDSUSCRIPTOR				=	this.navParams.data.record.idsuscriptor._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.estado	=	this.navParams.data.record.estado;
       this.nota			=	this.navParams.data.record.nota;
       this.isDisablednit =false
     

    }
  }

  manageGallery() : void
  {

    let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    this.estado='En proceso de confirmación de orden de pago.'
    }

     let   options       : any	     = { 
      userId:this.idsuscriptor ,
      nombre : this.idnombre,
      idperiodo:'5c5863254c69970d208e7a48',
       nota : 'Correcta.',
       estado:this.estado,
       bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' solicitud papa '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'asignapaps').then((result) => {
           //   if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'asignapaps').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }




  focused(selectedValue: any) {
    if(( this.cui) && (this.cui!=''))
    {

  


     this.items=[]
     this.manageGallery();

    }
     
   }

}



